\n                }\n            }\n    }\n}\n\n// Our Events Section Start\n.events-sec{\n    .events-sec-heading{\n\n    }\n    .events-sec-sub-heading{\n\n    }\n    .grid-container{\n        display: grid;\n        grid-template-columns: repeat(5, 1fr);\n        gap: 5px;\n        .grid-image1{\n            grid-column: span 2;\n            .image-container{\n                width: 100%;\n            }\n        }\n        .grid-image2{\n            grid-row: span 2;\n            .image-container{\n                width: 100%;\n            }\n        }\n        .grid-image3{\n            grid-row: span 3;\n            .image-container{\n                width: 100%;\n            }\n        }\n        .grid-image4{\n            .image-container{\n                width: 100%;\n            }\n        }\n        .grid-image5{\n            grid-row: span 2;\n            .image-container{\n                width: 100%;\n            }\n        }\n        .grid-image6{\n            grid-row: span 2;\n            .image-container{\n                width: 100%;\n            }\n        }\n        .grid-image7{\n            .image-container{\n                width: 100%;\n            }\n        }\n        .grid-image8{\n            .image-container{\n                width: 100%;\n            }\n        }\n        .grid-image9{\n            .image-container{\n                width: 100%;\n            }\n        }\n    }\n}\n\n//glimpse section start\n.glimpse-sec{\n    margin-top: 50px;\n    margin-bottom: 50px;\n    .glimpse-row{\n        align-items: center;\n        .glimpse{\n            display: flex;\n            .best-seller{\n                width: 20%;\n            }\n            .book-image{\n                width: 80%;\n                .glimpse-image{\n                    border-radius:15px;\n                }\n            }\n        }\n        \n        .glimpse-sec-heading{\n            font-family: $primary-font;\n            font-weight: 800;\n            @includ